<template>
  <button class="btn" @click="Click">
    <slot></slot>
  </button>
</template>

<script>
export default {
  name: 'Button',
  methods: {
    Click () {
      this.$emit('click')
    }
  }
}
</script>

<style lang="less">
  .btn{
    border: 0;
    border-radius: 100px;
    padding: 5px 10px;
    color: var(--main-font-color);
    background-color: var(--main-color);
    cursor: pointer;
   }
  .btn:hover{
    color: white;
    background-color: var(--theme-color);
  }
  .btn:focus{
    outline: 0;
  }
</style>
